/*


EC-CUBEbutton`.btn`<br>
<br>
<br>
 `.btn-ec-conversion`<br>
#FFFFFF<br>
#437EC4  #2963AB (hover)<br>
<br>
 `.btn-ec-delete`<br>
#FFFFFF<br>
#C04949  #A62E2E (hover)<br>
<br>
 `.btn-ec-regular`<br>
#595959  #262626 (hover)<br>
#FFFFFF  #F2F2F2 (hover)<br>
#595959  #262626 (hover)<br>
<br>
 `.btn-ec-sub`<br>
#FFFFFF<br>
#F5F6F8  #D6D9E0(hover)<br>
<br>
 `.btn-ec-actionIcon`<br>
#54687A<br>
#FFFFFF  #54687A (hover)<br>


Markup:
<!-- `style="margin-right: 5px;"` -->
<a href="" style="margin-right: 5px;" class="btn btn-ec-conversion"></a>
<a href="" style="margin-right: 5px;" class="btn btn-ec-delete"></a>
<a href="" style="margin-right: 5px;" class="btn btn-ec-regular"></a>
<a href="" style="margin-right: 5px;" class="btn btn-ec-sub"></a>
<a class="btn btn-ec-actionIcon"><i class="fa fa-close fa-lg text-secondary" aria-hidden="true"></i></a>

Styleguide 12.2
*/
.btn-ec-conversion{
  @include button-variant(#437EC4, transparent, #2963AB, transparent, #2963AB, transparent );
  cursor:pointer;
}

.btn-ec-delete{
  @include button-variant(#C04949, transparent, #A62E2E, transparent, #A62E2E, transparent );
  cursor:pointer;
}

.btn-ec-regular{
  @include button-variant(#FFFFFF, #595959, #F2F2F2, #262626, #F2F2F2, #262626 );
  color: #595959;
  cursor:pointer;
}

.btn-ec-regular:hover {
  color: #262626;
}

.btn-ec-sub{
  @include button-variant(#F5F6F8, transparent, #D6D9E0, transparent, #D6D9E0, transparent );
  color: #262626;
  cursor:pointer;
}

.btn-ec-sub:hover {
  color: #262626;
}

.btn-ec-actionIcon{
  @include button-variant(transparent, transparent, #EFF0F4, transparent, #EFF0F4, transparent );
  padding: 6px 12px;
  cursor:pointer;
}

.btn-ec-actionIcon:hover {
  color: #54687A;
}

/*


EC-CUBEbutton-group<br>
<br>
 `.btn-ec-tab`<br>
#595959  #262626 (hover)  #FFFFFF(active)<br>
#FFFFFF  #F2F2F2 (hover)  #595959(active)<br>
#595959  #262626 (hover)<br>
<br>

Markup:
<div class="btn-group nav d-inline-flex" id="pills-tab" role="tablist">
  <a class="nav-link active btn btn-ec-tab py-2 pl-4 pr-4" id="pills-pc-tab" data-toggle="pill" href="#pills-pc" role="tab" aria-controls="pills-pc" aria-selected="true">PC</a>
  <a class="nav-link btn btn-ec-tab py-2 pl-4 pr-4" id="pills-mobile-tab" data-toggle="pill" href="#pills-mobile" role="tab" aria-controls="pills-mobile" aria-selected="false"></a></div>

Styleguide 12.3
*/
.btn-ec-tab {
  @include button-variant(#FFFFFF, #595959, #F2F2F2, #262626, #F2F2F2, #262626 );
  color: #595959;
  cursor:pointer;
}

.btn-ec-tab:hover {
  color: #262626;
}

.btn-ec-tab.active{
  background-color: #595959 !important;
  color: #fff !important;
  box-shadow: none !important;
}

/*


EC-CUBEbadge`.badge`
<br>
 `badge-ec-blue`   :#437EC4;<br>
 `badge-ec-green`  :#25B877;<br>
 `badge-ec-red`    :#C04949;<br>
 `badge-ec-yellow` :#EEB128;<br>
<br>

Markup:
<!-- `style="margin-right: 5px;"` -->
<span style="margin-right: 5px;" class="badge badge-ec-blue"></span>
<span style="margin-right: 5px;" class="badge badge-ec-green"></span>
<span style="margin-right: 5px;" class="badge badge-ec-red"></span>
<span style="margin-right: 5px;" class="badge badge-ec-yellow"></span>
<span class="badge badge-ec-glay"></span>

Styleguide 12.4
*/
.badge-ec-blue{
  @include badge-variant(#fff);
  display: inline-block;
  border: 1px solid #437EC4;
  color: #437EC4;
  padding: .5rem 0.75rem;
  font-size: 14px;
  font-weight: normal;
}
.badge-ec-green{
  @include badge-variant(#fff);
  display: inline-block;
  border: 1px solid #25B877;
  color: #25B877;
  padding: .5rem 0.75rem;
  font-size: 14px;
  font-weight: normal;
}
.badge-ec-red{
  @include badge-variant(#fff);
  display: inline-block;
  border: 1px solid #C04949;
  color: #C04949;
  padding: .5rem 0.75rem;
  font-size: 14px;
  font-weight: normal;
}
.badge-ec-yellow{
  @include badge-variant(#fff);
  display: inline-block;
  border: 1px solid #EEB128;
  color: #EEB128;
  padding: .5rem 0.75rem;
  font-size: 14px;
  font-weight: normal;
}
.badge-ec-glay{
  @include badge-variant(#fff);
  display: inline-block;
  border: 1px solid #A3A3A3;
  color: #A3A3A3;
  padding: .5rem 0.75rem;
  font-size: 14px;
  font-weight: normal;
}

/*


EC-CUBEborder`.border`<br>
#ccc

Markup:
<!-- `style="padding: 20px; margin-bottom: 10px;"` -->
.d-block.border.border-ec-gray(style="padding: 20px; margin-bottom: 10px;") solid .border-ec-gray 
.d-block.border-ec-dashed(style="padding: 20px; margin-bottom: 10px;") dashed .border-ec-dashed

Styleguide 12.5
*/
.border-ec-gray {
  border-color: #ccc !important;
}
.border-ec-dashed{
  border: $border-width dashed #ccc !important;
}

/*


EC-CUBEbackground-color

Markup:
<!-- `style="padding: 20px; margin-bottom: 10px;"` -->
.d-block.bg-ec-lightGray(style="padding: 20px; margin-bottom: 10px;")  .bg-ec-lightGray #f9f9f9
.d-block.bg-ec-formGray(style="padding: 20px; margin-bottom: 10px;")  .bg-ec-formGray #F8F9FA

Styleguide 12.6
*/
.bg-ec-lightGray{
  background-color: #f9f9f9 !important;
}
.bg-ec-formGray{
  background-color: #F8F9FA !important;
}

/*


EC-CUBEcolor

Markup:
.text-ec-gray  .text-ec-gray#999
.text-ec-lightGray  .text-ec-lightGray#ccc

Styleguide 12.7
*/
.text-ec-gray { color: #999 !important; }
.text-ec-lightGray { color: #ccc !important; }
